@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
    box-sizing: border-box;
}
body{
    background-color: black;
    color: white;
    font-family: 'Roboto', sans-serif;
}
.N{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    /* justify-content: center; */
    /* align-items: center; */
    text-align: center;
}
.N h1{
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: monospace  ;
    font-size: 5rem;
}
.N h2{
    flex-wrap: wrap;
    font-size: 2rem;
}
.N button{
    align-items: center;
    justify-content: center;
    width: 10rem;
    border-style: ridge;
    border-collapse: collapse;

    overflow: hidden;
}
.intro{ 
 display: flex;
 justify-content: center;
 /* margin: 2em; */
 flex-wrap: wrap;
overflow: hidden;
}
.intro img{ 
    height: 40rem;
    flex-wrap: wrap;
}
.intro p{
    flex-wrap: wrap;
    font-size: 1.4rem;
}
